<template>
	<div class="site_box">
		<!--添加地址-->
		<header>
			<Headnav head_left="" head_title='账户信息' head_right=" ">
				<router-link to="/user" slot="left" tag="span">
					<van-icon name="arrow-left" />
				</router-link>
			</Headnav>
		</header>
		<div class="site_cont">
			<ul class="site_username">
				<li>
					<p>头像</p>
					<p>
						<input type="file" @change="upload($event)" ref="upload">
						<img :src="'//elm.cangdu.org/img/'+listShow.avatar" alt="">
						<!--<img :src="" alt="">-->
						<span class="el-icon-arrow-right"></span>
					</p>
				</li>
				<li>
					<p>用户名</p>
					<p>
						<span class="el-icon-arrow-right"></span>
					</p>
				</li>
					<router-link to="/shopSite" tag="li">
						<p>收获地址</p>
					<p>
						<span class="el-icon-arrow-right"></span>
					</p>
					</router-link>
			</ul>
			<p class="site_text">账号绑定</p>
			<div class="site_phone">
				<p><span class=el-icon-mobile-phone></span>手机</p>
				<p class="el-icon-arrow-right"></p>
			</div>
			<p class="site_text">安全设置</p>
			<div class="site_phone">
				<p>登录密码</p>
				<p>
					<span>修改</span>
					<span class="el-icon-arrow-right"></span>
				</p>
			</div>
			<p class="site_btn">
				<button @click="delElem()">退出登录</button>
			</p>
		</div>
		<footer>
			<Footnav></Footnav>
		</footer>
	</div>
</template>

<script>
	import Headnav from '.././About/headNav'
	import Footnav from '.././About/footNav'
	import {mapMutations,mapState} from 'vuex'
	export default {
		name: "site",
		data(){
			return{
				headPhoto:{},
				start:false
			}
		},
		methods:{
			...mapMutations(['delElem','getlocalStorage']),
			//点击input换头像
			upload(){
				const userUploadAtt= (File,config) =>this.$axios.post(`http://elm.cangdu.org/eus/v1/users/${this.listShow.userId}/avatar`,File,config)
				let input =this.$refs.upload
				//new 一个对象
				let data  = new FormData()
				//用FormData.append为表单添加建值对到表单中
				data.append('file',input.files[0])
				userUploadAtt(data,{headers:{'Content-Type': 'multipart/form-data'}}).then(res =>{
					this.listShow.avatar = res.data.image_path;
					this.$store.commit("upLoad",this.listShow)
				})
			}
		},
		mounted(){
			// this.id =JSON.parse(localStorage.getItem('Eleme'))
			// console.log(this.id)
			this.getlocalStorage()
		},
		computed:{
			...mapState(['listShow'])
		},
		components:{
			Headnav,
			Footnav,
		}
	}
</script>
